export class Catalog {
    constructor(events) {
        this.items = [];
        this.events = events;
    }
    setItemList(items) {
        this.items = Array.isArray(items) ? items : [];
        this.events.emit('productList:changed', this.items);
    }
    getItemList() {
        return this.items;
    }
    getItem(productId) {
        return this.items.find((item) => item.id == productId.id);
    }
}
