export class WebLarekApi {
    constructor(API) {
        this.API = API;
    }
    productList() {
        return this.API.get('/product').then((value) => {
            return value.items;
        });
    }
    productItem(id) {
        return this.API.get(`/product/${id}`).then((product) => {
            return product;
        });
    }
    orderProduct(order) {
        return this.API.post('/order', order);
    }
}
